.lds-dual-ring {
  width: 80px;
  height: 80px;
  margin: auto;
  display: block;
}

.lds-dual-ring:after {
  content: " ";
  width: 64px;
  height: 64px;
  border: 6px solid #b92f2c;
  border-color: #b92f2c rgba(0, 0, 0, 0);
  border-radius: 50%;
  margin: 8px;
  animation: lds-dual-ring 1.2s linear infinite;
  display: block;
}

@keyframes lds-dual-ring {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.wrep {
  min-height: 100%;
