  display: flex;
}

.main {
  flex: auto;
}

.spinner-box {
  position: absolute;
}

.preloader {
  color: #b92f2c;
  z-index: 1001;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: hidden;
}

.preloader__image {
  width: 70px;
  height: 70px;
  text-align: center;
  margin-top: -35px;
  margin-left: -35px;
  animation: preloader-rotate 2s linear infinite;
  position: relative;
  top: 50%;
  left: 50%;
}

@keyframes preloader-rotate {
  100% {
    transform: rotate(360deg);
  }
}

.loaded_hiding .preloader {
  opacity: 0;
  transition: opacity 1s;
}

.loaded .preloader {
  display: none;
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  display: none;
}

